const TingleUI = require('tingle-ui');
let { Progress, Button, Icon, ActionSheet, Popup } = TingleUI;

class Page extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            percent1: 0,
            percent2: 30,
            percent3: 70,
        };
        
    }

    render() {
        const t = this;

        if (t.state.percent1 < 100) {
        setTimeout(() => {
            t.setState({
                percent1: t.state.percent1 + 20,
                });
            }, 1000);
        }

        return (
        <div className="page-extra">
            <div style={{ marginBottom: '30px' }}>
                <Progress percent={t.state.percent1} status="normal" showInfo />
            </div>
            <div style={{ marginBottom: '30px' }}>
                <Progress percent={t.state.percent2} status="exception" showInfo={false} />
            </div>
            <div style={{ marginBottom: '30px' }}>
                <Progress percent={t.state.percent3} showInfo strokeWidth={1} />
            </div>
            <div>
                <Button onClick={() => {
                ActionSheet.show({
                        options: ['', '', '', ''],
                        destructiveButtonIndex: 3,
                        message: '',
                        maskClosable: false,
                    }, (index) => {
                        console.log(index);
                    });
                }}
                ></Button>
            </div>
            <div>
                <Button onClick={() => {
                Popup.show(<div className="demo-popup-container"></div>, {
                    animationType: 'slide-up',
                });
                }}
                ></Button>
                <Button onClick={() => {
                Popup.show(<div className="demo-popup-container"></div>, {
                    animationType: 'slide-down',
                });
                }}
                ></Button>
                <Button onClick={() => {
                Popup.show(<div className="demo-popup-container-2"></div>, {
                    animationType: 'slide-right',
                });
                }}
                ></Button>
                <Button onClick={() => {
                Popup.show(<div className="demo-popup-container-2"></div>, {
                    animationType: 'slide-left',
                });
                }}
                ></Button>
                <Button onClick={() => {
                Popup.show(
                    <div className="demo-popup-container-2">
                    <div onClick={() => {
                        Popup.hide();
                    }}
                    > popup</div>
                    </div>, {
                    maskClosable: false,
                    });
                }}
                > Popup</Button>
            </div>
        </div>
        );
    }
}

module.exports = Page;



